export class LTokenContract extends Contract {
  constructor(chainId, contractAddress, poolAddress, isProvider) {
    super(chainId, contractAddress, isProvider);
    this.poolAddress = poolAddress;
    this.contract = new this.web3.eth.Contract(
      CONTRACT_ABI,
      this.contractAddress
    );
  }

  async balance(accountAddress) {
    const res = deriToNatural(await this._call('balanceOf', [accountAddress]));
    //console.log("> lToken balance", res.toString())
    return res;
  }

  async totalSupply() {
    const res = deriToNatural(await this._call('totalSupply'));
    //console.log("> lToken totalSupply", res.toString())
    return res;
  }
}
